.product {
  padding: 30px 0;
  background-color: $blue-light;
  @include screen(laptop) {
    padding: 60px 0;
  }
  @include screen(desktop) {
    padding: 70px 0;
  }
  &__wrapper {
    display: grid;
    align-items: center;
    gap: 20px;
    @include screen(laptop) {
      gap: 40px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__content {
    @include screen(laptop) {
      grid-column: span 8 / span 8;
    }
    p {
      font-size: 14px;
      line-height: 17px;
      margin-top: 16px;
      color: #69737D;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
      &:first-child {
        margin-top: 0;
      }
    }
  }
  &__image {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      grid-column: span 4 / span 4;
    }
    img {
      max-width: 200px;
      margin: 0 auto;
      @include screen(laptop) {
        max-width: 100%;
        position: absolute;
      }
    }
  }
  &__info {
    &__wrapper {
      display: grid;
      gap: 20px;
      @include screen(laptop) {
        gap: 40px;
        grid-template-columns: repeat(3, minmax(0, 1fr));
      }
      p {
        color: $black-primary;
        font-size: 16px;
        line-height: 20px;
        @include screen(desktop) {
          font-size: 20px;
          line-height: 25px;
        }
      }
    }
  }
}